import React from 'react';
import Modal from './Modal';
import InfoCircleO from '@wind/icons/lib/icons/InfoCircleO';
import CheckCircleO from '@wind/icons/lib/icons/CheckCircleO';
import CloseCircleO from '@wind/icons/lib/icons/CloseCircleO';
import ExclamationCircleO from '@wind/icons/lib/icons/ExclamationCircleO';
import confirm from './confirm';
Modal.info = function (props) {
    const config = Object.assign({ type: 'info', icon: <InfoCircleO />, okCancel: false }, props);
    return confirm(config);
};
Modal.success = function (props) {
    const config = Object.assign({ type: 'success', icon: <CheckCircleO />, okCancel: false }, props);
    return confirm(config);
};
Modal.error = function (props) {
    const config = Object.assign({ type: 'error', icon: <CloseCircleO />, okCancel: false }, props);
    return confirm(config);
};
Modal.warning = Modal.warn = function (props) {
    const config = Object.assign({ type: 'warning', icon: <ExclamationCircleO />, okCancel: false }, props);
    return confirm(config);
};
Modal.confirm = function (props) {
    const config = Object.assign({ type: 'confirm', okCancel: true }, props);
    return confirm(config);
};
export default Modal;
